html, body, #root, #root > div {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height: 100%;
  width: 100%;
}


#content {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
}

.content {
  max-width: 100vw;
  min-width: 80vw;
  height: 100%;
  padding: 0 2em 2em 2em;
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}

@media (min-width: 1200px) {
  .content {
    max-width: 80vw;
  }
}

#chart {
  -webkit-box-shadow: 0 0 15px;
          box-shadow: 0 0 15px;
  border-radius: 4px;
}

#chart > div {
  border-radius: 4px;
}



